<template>
    <div class="container">
        <el-container>
            <el-header height="100" class="hidden-sm-and-down">
                <el-row>
                    <el-col :span="12" class="nav-left" type="flex">
                        <div class="icon"></div>
                        <h1>清华大学新医盟科室共建平台</h1>
                    </el-col>
                    <el-col :span="12" class="nav-right">
                        <ul>
                            <li><a href="http://www.yiliaolianmeng.org/platform/index.html">首页</a></li>
                            <li><a href="javascript:;">会诊中心</a></li>
                            <li><a href="http://www.yiliaolianmeng.org">清华新医盟</a></li>
                        </ul>
                    </el-col>
                </el-row>
            </el-header>
            <el-header height="100" class="hidden-md-and-up">
                <el-row class="sm-nav">
                    <i style="28px" class="el-icon-tickets" @click="menuShow = !menuShow"></i>
                    <h1>清华大学新医盟科室共建平台</h1>
                </el-row>
            </el-header>
        </el-container>
        <transition name="mobile-menu">
            <ul class="sub-menu" v-show="menuShow">
                <li><a href="http://www.yiliaolianmeng.org/platform/index.html">首页</a></li>
                <li><a href="javascript:;">会诊中心</a></li>
                <li><a href="http://www.yiliaolianmeng.org">清华新医盟</a></li>
            </ul>
        </transition>
        <div class="login-container">
            <h2>远程会诊中心</h2>
            <div class="input-container">
                <el-input v-model="userName" placeholder="用户名" prefix-icon="el-icon-edit-outline">
                </el-input>
                <el-input type="password" v-model="password" placeholder="密码" prefix-icon="el-icon-view">
                </el-input>
                <el-row :gutter="20" type="flex">
                    <el-col :span="12">
                        <el-input v-model="captcha" style="margin-top:0;" placeholder="验证码" @keyup.enter.native="submit"></el-input>
                    </el-col>
                    <el-col :span="8" v-html="capture_img"></el-col>
                    <el-col :span="4" @click.native="getCapture"><i class="el-icon-refresh"></i></el-col>
                </el-row>
                <el-button @click="submit" type="primary">登陆</el-button>
            </div>
            <h3>客服专线：400-112-6996</h3>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    mounted(){
        this.getCapture();
    },
    data(){
        return {
            userName:'',
            password:'',
            menuShow:false,
            capture_img:'',
            captcha:''
        }
    },
    methods:{
        submit(){
            axios.post('/session/create',{
                username:this.userName,
                password:this.password,
                captcha:this.captcha
            }).then((response)=>{
                let res = response.data;
                console.log(res);
            })
        },
        getCapture(){
            axios.get('/session/captcha').then((response)=>{
                let res = response.data;
                this.capture_img = res;
            })
        }
    }
}
</script>

<style lang="scss" scoped>
.container{
    width: 100%;
    height: 100%;
    background: url('./../assets/images/container_bg.png') no-repeat center center fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    background-size: cover;
    .el-container{
        padding: 0 50px;
        background: rgba(246, 249, 253, 0.6);
        .el-header{
            height: 100px;
            line-height: 100px;
            .nav-left{
                align-items: center;
                display: flex;
                .icon{
                    width: 52px;
                    height: 52px;
                    background: url('./../assets/images/logo.png') no-repeat center center;
                }
                h1{
                    margin-left: 15px;
                    font-size: 24px;
                    color:rgb(58,119,213);
                }
            }
            .nav-right{
                ul{
                    display: flex;
                    justify-content: flex-end;
                    li{
                        cursor: pointer;
                        padding-left: 60px;
                        a{
                            font-size: 18px;
                            color: #333;
                            &:hover{
                                color: rgb(58,119,213);
                            }
                        }
                    }
                }
            }
            .sm-nav{
                font-size: 20px;
                color:rgb(58,119,213);
                text-align: center;
                i{
                    position: absolute;
                    left: 0;
                    top: 50%;
                    transform: translateY(-50%);
                    cursor: pointer;
                }
            }
        }
    }
    .sub-menu{
        background: rgba(246, 249, 253, 0.6);
        text-align: center;
        li{
            padding: 10px 0;
            a{
                color:rgb(58,119,213);
            }
        }
    }
    .login-container{
        background-color: #fff;
        width: 375px;
        position: absolute;
        top: 24%;
        right: 10%;
        box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.4);
        h2{
            text-align: center;
            font-size: 26px;
            margin-top: 50px;
            color: #333;
        }
        .input-container{
            padding: 15px;
            .el-input{
                margin-top: 15px;
            }
            .el-row{
                margin-top: 15px;
                .el-col{
                    text-align: center;
                    line-height: 40px;
                    i{
                        font-size: 26px;
                    }
                }
            }
            .el-button{
                margin-top: 15px;
                border-radius: 0;
                width: 100%;
            }
        }
        h3{
            text-align: center;
            color: #777;
            font-size: 12px;
            margin-top: 15px;
            margin-bottom: 30px;
        }
    }
}
.mobile-menu-enter-active, .mobile-menu-leave-active {
    transition: all .3s;
}
.mobile-menu-enter, .mobile-menu-leave-to {
    height: 0;
    li{
        display: none;
    }
}
.mobile-menu-enter-to, .mobile-menu-leave {
    height: 90px;
    li{
        display: none;
    }
}
</style>



