<template>
    <div>
        {{title}}
    </div>
</template>

<script>
import axios from 'axios';
export default {
    mounted(){
        this.getData();
    },
    data(){
        return{
            title:''
        }
    },
    methods:{
        getData(){
            axios.get('/login').then((response)=>{
                let res = response.data;
                this.title = res.title;
            })
        }
    }
}
</script>

<style lang="scss">
</style>



